body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Permanent Marker', cursive;
}
body {
    background: url("http://getwallpapers.com/wallpaper/full/e/9/8/271889.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
    background-position: center;
    width: 100vw;
}
.navbar {
    width: 100%;
    background: transparent;
    display: flex;
    min-height: 50px;
    height: auto;
    justify-content: center;
    align-items: center;
}
.nav-content{
    font-size: 4rem;
    color: yellow;
}
.refreshButton {
    text-align: center;
    margin: auto;
    margin-top: 10px;
}
.refreshButton button {
    border-radius: 10px;
    outline: none;
    padding: 10px;
    border: none;
    background: transparent;
    cursor: pointer;
    width: 100px;
    color: yellow;
    background: rgba(0,0,0,0.5);
}
.quotesAuthor {
    text-align: right;
}

.quoteBox {
    width: 60%;
    min-height: 200px;
    max-height: 400px;
    border: 2px solid white;
    text-align: center;
    margin: auto;
    margin-top: 3em; 
    background: rgba(0,0,0,0.5);
    border-radius: 2em;   
}
.quotes {
    width: 100%;
    font-size: 2rem;
    color: yellow;
    padding: 0 1.5rem;
}
.quotesAuthor {
    width: 100%;
    font-size: 1.5rem;
    color: yellow;
    padding-right: 1rem;
}
